@import "utils/fonts";
@import "common/global";
@import "common/header";

.main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  text-align: center;
  background: url("../img/main/bg.jpg") no-repeat center / cover;

  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  &__title {
    font-weight: 700;
    font-size: 152px;
    line-height: 100%;
    text-transform: uppercase;
    color: #fff;
  }

  &__subtitle {
    margin-top: 20px;
    font-weight: 500;
    font-size: 24px;
    letter-spacing: 20px;
    text-transform: uppercase;
    color: #11749e;
  }

  &-down {
    position: relative;
    top: 15vh;

    &__text {
      margin-bottom: 15px;
      font-size: 13px;
      color: #fff;
    }

    &__link {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto;
      border: 1px solid #fff;
      border-radius: 50%;
      width: 66px;
      height: 66px;
      text-decoration: none;
    }
  }
}

.about {
  padding-top: 170px;
  padding-bottom: 130px;
  background: $blue;

  &__row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__title {
    position: relative;
    z-index: 1;
    margin-bottom: 140px;

    &::before {
      content: "w";
      position: absolute;
      left: 50%;
      top: 0;
      z-index: 0;
      font-size: 200px;
      text-transform: uppercase;
      color: #fff;
      opacity: 0.2;
      transform: translateX(-50%);
    }
  }

  &-block {
    max-width: 23%;
    text-align: center;

    &__icon {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto;
      border-radius: 50%;
      width: 130px;
      height: 130px;
      background-color: rgba(255, 255, 255, 0.1);

      img {
        width: 36px;
      }

      span {
        font-size: 28px;
        color: #fff;
      }
    }

    &__title {
      margin-top: 18px;
      margin-bottom: 30px;
      font-weight: 500;
      font-size: 18px;
      color: #fff;
    }

    &__text {
      font-weight: 400;
      font-size: 15px;
      color: #fff;
    }
  }
}

.news {
  overflow: hidden;

  &__container {
    margin: 0 auto;
    padding: 0 20px;
    width: 1220px;
  }

  &__content {
    flex: 0 0 100%;
  }

  &__img {
    position: relative;
    flex: 0 0 50%;
    transform: translate(-100%, 0);

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__body {
    padding: 80px 0;
    width: 50%;
  }

  &__date {
    font-weight: 400;
    font-size: 13px;

    span {
      display: inline-block;
      margin: 0 12px;
    }
  }

  &__title {
    margin-top: 15px;
    margin-bottom: 40px;
    font-weight: 500;
    font-size: 25px;
    text-transform: uppercase;
  }

  &__text {
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 15px;
  }

  &__block {
    &_1 {
      display: flex;
      color: #fff;
      background: #222;

      .news__body {
        padding-right: 85px;
      }
    }

    &_2 {
      display: flex;
      color: #222;
      background: #f3f3f3;

      .news__content {
        transform: translate(-50%, 0);
      }

      .news__body {
        margin-left: auto;
        padding-left: 85px;
        width: 50%;
      }

      .news__img {
        transform: translate(0, 0);
      }
    }
  }
}

@import "common/footer";
